* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
body {
  position: absolute;
  top: 0;
  left: 0;

  display: block;

  min-width: 100%;
  min-height: 100%;
  margin: 0 auto;

  color: $text-color;

  font-size: 16px;
}
.main-img {
  position: fixed;
  z-index: -1;
  top: 0;
  left: 0;

  min-width: 100%;
  min-height: 100%;
}

.js-inactive {
  display: none;
}
.js-active {
  display: block;
}
